	data() {
		return {
				isactive:1,
				isActive:false,
				issure:false
		}
	},
	methods: {
		sureselect(index){
			this.isactive = index
		},
		coupon(){
			this.isActive = true;
		},
		close(){
			this.isActive = false;
		},
		sure(){
			this.issure = true;
			this.isActive = false
		},
